.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
    background-color: #fff;
    border-bottom: 1px solid #e0e0e0;
  }
  
  .header__logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .header__logo-img {
    height: 55px;
  }
  
  .header__nav {
    font-family: "Rubik", serif;
    color: rgba(0, 0, 0, 0.7216);
    display: flex;
    gap: 90px;
  }
  
  .header__nav a {
    text-decoration: none;
    color: #333;
    font-size: 23px;
  }
  
  .header__actions {
    display: flex;
    gap: 30px;
  }
  
  .header__download {
    border: none;
    font-size: 16px;
    cursor: pointer;
    background-color: #4A90E2;
    font-family: "Rubik", serif;
    font-weight: bold;
    color: #fff;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.47);
    width: 135px;
    height: 50px;
  }

  .header__login img {
    height: 40px;
  }

  .header__login {
    align-items: center;
    gap: 10px;
    display: flex;
  }

  .header__login p {
    font-family: "Rubik", serif;
    font-size: 21px;
  }
  
  .button_down {
    display: flex;
    align-items: center;
  }
  

  @media (max-width: 768px) {
    .header {
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;
    }
  
    .header__nav {
      flex-wrap: wrap;
      gap: 10px;
    }
  
    .header__actions {
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
  }
  